"use client";

import { useState, useEffect, useCallback } from "react";
import { useParams, useRouter } from "next/navigation";
import { motion, AnimatePresence } from "framer-motion";
import { Chess } from "chess.js";
import { Chessboard } from "react-chessboard";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { useAuth } from "@/context/AuthContext";
import socketService from "@/lib/socket";
import { Room, Move } from "@/lib/api";
import { formatTime, cn } from "@/lib/utils";
import {
    Flag,
    Handshake,
    MessageSquare,
    ChevronLeft,
    ChevronRight,
    Send,
    X,
    Crown,
    Clock,
} from "lucide-react";

interface ChatMessage {
    id: string;
    username: string;
    message: string;
    timestamp: string;
    isGuest: boolean;
}

export default function GamePage() {
    const params = useParams();
    const router = useRouter();
    const { user } = useAuth();
    const roomId = params.roomId as string;

    // Game state
    const [game, setGame] = useState(new Chess());
    const [room, setRoom] = useState<Room | null>(null);
    const [playerColor, setPlayerColor] = useState<"white" | "black">("white");
    const [isMyTurn, setIsMyTurn] = useState(false);
    const [selectedSquare, setSelectedSquare] = useState<string | null>(null);
    const [legalMoves, setLegalMoves] = useState<string[]>([]);

    // Timers
    const [myTime, setMyTime] = useState(600);
    const [opponentTime, setOpponentTime] = useState(600);

    // Move history
    const [moveHistory, setMoveHistory] = useState<string[]>([]);
    const [currentMoveIndex, setCurrentMoveIndex] = useState(-1);

    // Chat
    const [chatMessages, setChatMessages] = useState<ChatMessage[]>([]);
    const [chatInput, setChatInput] = useState("");
    const [isChatOpen, setIsChatOpen] = useState(false);

    // Game end
    const [gameResult, setGameResult] = useState<{ winner: string; reason: string } | null>(null);

    // Draw offer
    const [drawOffered, setDrawOffered] = useState(false);
    const [drawOfferedBy, setDrawOfferedBy] = useState<string | null>(null);

    // Opponent status
    const [opponentDisconnected, setOpponentDisconnected] = useState(false);

    // Initialize socket listeners
    useEffect(() => {
        const socket = socketService.getSocket();
        if (!socket) {
            router.push("/dashboard");
            return;
        }

        // Join the room
        socketService.joinRoom(roomId);

        // Room update
        const unsubRoomUpdate = socketService.onRoomUpdate((data) => {
            setRoom(data.room);
            updateGameState(data.room);
        });

        // Game started
        const unsubGameStarted = socketService.onGameStarted((data) => {
            setRoom(data.room);
            updateGameState(data.room);
        });

        // Move made
        const unsubMoveMade = socketService.onMoveMade((data) => {
            const newGame = new Chess(data.fen);
            setGame(newGame);
            setMoveHistory((prev) => [...prev, data.move.san || `${data.move.from}-${data.move.to}`]);
            setCurrentMoveIndex((prev) => prev + 1);
            setIsMyTurn(data.currentTurn === playerColor);
        });

        // Time update
        const unsubTimeUpdate = socketService.onTimeUpdate((data) => {
            if (room) {
                const myPlayer = room.player1.color === playerColor ? "player1" : "player2";
                const opPlayer = myPlayer === "player1" ? "player2" : "player1";
                setMyTime(myPlayer === "player1" ? data.player1TimeLeft : data.player2TimeLeft);
                setOpponentTime(opPlayer === "player1" ? data.player1TimeLeft : data.player2TimeLeft);
            }
        });

        // Game ended
        const unsubGameEnded = socketService.onGameEnded((data) => {
            setGameResult(data.result);
        });

        // Chat
        const unsubChat = socketService.onChatMessage((data) => {
            setChatMessages((prev) => [...prev, data]);
        });

        // Draw offer
        const unsubDrawOffered = socketService.onDrawOffered((data) => {
            setDrawOfferedBy(data.username);
            setDrawOffered(true);
        });

        const unsubDrawDeclined = socketService.onDrawDeclined(() => {
            setDrawOffered(false);
            setDrawOfferedBy(null);
        });

        // Opponent disconnect
        const unsubOpponentDisconnected = socketService.onOpponentDisconnected(() => {
            setOpponentDisconnected(true);
        });

        const unsubOpponentReconnected = socketService.onOpponentReconnected(() => {
            setOpponentDisconnected(false);
        });

        // Reconnection
        const unsubReconnected = socketService.onGameReconnected((data) => {
            setRoom(data.room);
            updateGameState(data.room);
        });

        return () => {
            unsubRoomUpdate();
            unsubGameStarted();
            unsubMoveMade();
            unsubTimeUpdate();
            unsubGameEnded();
            unsubChat();
            unsubDrawOffered();
            unsubDrawDeclined();
            unsubOpponentDisconnected();
            unsubOpponentReconnected();
            unsubReconnected();
        };
    }, [roomId, playerColor, room]);

    const updateGameState = (roomData: Room) => {
        const newGame = new Chess(roomData.gameData.fen);
        setGame(newGame);

        // Determine player color
        const myId = user?.id || user?._id || user?.guestId;
        if (roomData.player1.userId === myId || roomData.player1.guestId === myId) {
            setPlayerColor(roomData.player1.color);
            setMyTime(roomData.player1.timeLeft);
            setOpponentTime(roomData.player2.timeLeft);
        } else {
            setPlayerColor(roomData.player2.color);
            setMyTime(roomData.player2.timeLeft);
            setOpponentTime(roomData.player1.timeLeft);
        }

        setIsMyTurn(roomData.currentTurn === playerColor);
    };

    // Handle square click
    const onSquareClick = (square: string) => {
        if (!isMyTurn || gameResult) return;

        if (selectedSquare) {
            // Try to make a move
            const move = { from: selectedSquare, to: square, promotion: "q" };
            try {
                const result = game.move(move);
                if (result) {
                    socketService.makeMove(roomId, move);
                    setSelectedSquare(null);
                    setLegalMoves([]);
                }
            } catch {
                // Invalid move, select new piece
                selectPiece(square);
            }
        } else {
            selectPiece(square);
        }
    };

    const selectPiece = (square: string) => {
        const piece = game.get(square);
        if (piece && piece.color === playerColor[0]) {
            setSelectedSquare(square);
            const moves = game.moves({ square, verbose: true });
            setLegalMoves(moves.map((m) => m.to));
        } else {
            setSelectedSquare(null);
            setLegalMoves([]);
        }
    };

    // Handle piece drop (drag and drop)
    const onDrop = (sourceSquare: string, targetSquare: string) => {
        if (!isMyTurn || gameResult) return false;

        const move = { from: sourceSquare, to: targetSquare, promotion: "q" };
        try {
            const result = game.move(move);
            if (result) {
                socketService.makeMove(roomId, move);
                setSelectedSquare(null);
                setLegalMoves([]);
                return true;
            }
        } catch {
            return false;
        }
        return false;
    };

    const handleResign = () => {
        if (confirm("Are you sure you want to resign?")) {
            socketService.resign(roomId);
        }
    };

    const handleOfferDraw = () => {
        socketService.offerDraw(roomId);
    };

    const handleDrawResponse = (accept: boolean) => {
        socketService.respondToDraw(roomId, accept);
        setDrawOffered(false);
        setDrawOfferedBy(null);
    };

    const handleSendChat = () => {
        if (chatInput.trim()) {
            socketService.sendMessage(roomId, chatInput.trim());
            setChatInput("");
        }
    };

    // Custom square styles
    const customSquareStyles: Record<string, React.CSSProperties> = {};
    if (selectedSquare) {
        customSquareStyles[selectedSquare] = {
            backgroundColor: "rgba(220, 38, 38, 0.4)",
        };
    }
    legalMoves.forEach((square) => {
        customSquareStyles[square] = {
            background: game.get(square)
                ? "radial-gradient(circle, rgba(220, 38, 38, 0.6) 85%, transparent 85%)"
                : "radial-gradient(circle, rgba(220, 38, 38, 0.4) 25%, transparent 25%)",
        };
    });

    const opponentName = room
        ? playerColor === room.player1.color
            ? room.player2.guestUsername || "Opponent"
            : room.player1.guestUsername || "Opponent"
        : "Opponent";

    const myName = user?.username || "You";

    return (
        <div className="min-h-screen bg-background p-4 flex">
            {/* Main Game Area */}
            <div className="flex-1 flex flex-col items-center justify-center">
                {/* Opponent Info */}
                <div className="w-full max-w-lg flex items-center justify-between mb-4">
                    <div className="flex items-center gap-3">
                        <Avatar>
                            <AvatarFallback>{opponentName.slice(0, 2).toUpperCase()}</AvatarFallback>
                        </Avatar>
                        <div>
                            <div className="font-semibold text-white">{opponentName}</div>
                            {opponentDisconnected && (
                                <div className="text-xs text-accent">Disconnected</div>
                            )}
                        </div>
                    </div>
                    <div className={cn(
                        "font-mono text-2xl px-4 py-2 rounded",
                        !isMyTurn && room?.gameStatus === "in_progress"
                            ? "bg-accent text-white"
                            : "bg-background-elevated text-foreground-muted"
                    )}>
                        {formatTime(opponentTime)}
                    </div>
                </div>

                {/* Chessboard */}
                <div className="relative">
                    <Chessboard
                        position={game.fen()}
                        onSquareClick={onSquareClick}
                        onPieceDrop={onDrop}
                        boardOrientation={playerColor}
                        customSquareStyles={customSquareStyles}
                        boardWidth={560}
                        customBoardStyle={{
                            borderRadius: "4px",
                            boxShadow: "0 0 30px rgba(0,0,0,0.5)",
                        }}
                        customDarkSquareStyle={{ backgroundColor: "#1a1a1a" }}
                        customLightSquareStyle={{ backgroundColor: "#2a2a2a" }}
                    />

                    {/* Turn indicator overlay */}
                    {isMyTurn && room?.gameStatus === "in_progress" && (
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            className="absolute -bottom-2 left-1/2 -translate-x-1/2 bg-accent px-4 py-1 rounded-full text-sm font-semibold"
                        >
                            YOUR TURN
                        </motion.div>
                    )}
                </div>

                {/* Player Info */}
                <div className="w-full max-w-lg flex items-center justify-between mt-4">
                    <div className="flex items-center gap-3">
                        <Avatar>
                            <AvatarFallback>{myName.slice(0, 2).toUpperCase()}</AvatarFallback>
                        </Avatar>
                        <div>
                            <div className="font-semibold text-white">{myName}</div>
                            <div className="text-xs text-foreground-muted">{playerColor.toUpperCase()}</div>
                        </div>
                    </div>
                    <div className={cn(
                        "font-mono text-2xl px-4 py-2 rounded",
                        isMyTurn && room?.gameStatus === "in_progress"
                            ? "bg-accent text-white animate-pulse"
                            : "bg-background-elevated text-foreground-muted"
                    )}>
                        {formatTime(myTime)}
                    </div>
                </div>

                {/* Game Controls */}
                <div className="flex gap-4 mt-6">
                    <Button variant="secondary" onClick={handleResign} disabled={!!gameResult}>
                        <Flag className="w-4 h-4" />
                        RESIGN
                    </Button>
                    <Button variant="secondary" onClick={handleOfferDraw} disabled={!!gameResult || drawOffered}>
                        <Handshake className="w-4 h-4" />
                        OFFER DRAW
                    </Button>
                    <Button variant="secondary" onClick={() => setIsChatOpen(!isChatOpen)}>
                        <MessageSquare className="w-4 h-4" />
                        CHAT
                    </Button>
                </div>
            </div>

            {/* Side Panel */}
            <div className="w-80 flex flex-col gap-4">
                {/* Move History */}
                <Card className="flex-1 p-4 overflow-hidden flex flex-col">
                    <h3 className="font-display text-lg mb-4">MOVE HISTORY</h3>
                    <div className="flex-1 overflow-y-auto font-mono text-sm">
                        {moveHistory.reduce((acc, move, index) => {
                            if (index % 2 === 0) {
                                acc.push(
                                    <div key={index} className="flex gap-4 py-1 hover:bg-white/5">
                                        <span className="text-foreground-dim w-8">{Math.floor(index / 2) + 1}.</span>
                                        <span className="w-16">{move}</span>
                                        <span className="w-16">{moveHistory[index + 1] || ""}</span>
                                    </div>
                                );
                            }
                            return acc;
                        }, [] as JSX.Element[])}
                    </div>
                </Card>

                {/* Chat Panel */}
                <AnimatePresence>
                    {isChatOpen && (
                        <motion.div
                            initial={{ opacity: 0, height: 0 }}
                            animate={{ opacity: 1, height: 300 }}
                            exit={{ opacity: 0, height: 0 }}
                        >
                            <Card className="h-full p-4 flex flex-col">
                                <div className="flex items-center justify-between mb-4">
                                    <h3 className="font-display text-lg">CHAT</h3>
                                    <button onClick={() => setIsChatOpen(false)}>
                                        <X className="w-4 h-4 text-foreground-muted" />
                                    </button>
                                </div>
                                <div className="flex-1 overflow-y-auto space-y-2 mb-4">
                                    {chatMessages.map((msg) => (
                                        <div key={msg.id} className="text-sm">
                                            <span className="text-accent font-semibold">{msg.username}: </span>
                                            <span className="text-foreground-muted">{msg.message}</span>
                                        </div>
                                    ))}
                                </div>
                                <div className="flex gap-2">
                                    <Input
                                        value={chatInput}
                                        onChange={(e) => setChatInput(e.target.value)}
                                        onKeyDown={(e) => e.key === "Enter" && handleSendChat()}
                                        placeholder="Type message..."
                                        className="flex-1"
                                    />
                                    <Button onClick={handleSendChat} size="icon">
                                        <Send className="w-4 h-4" />
                                    </Button>
                                </div>
                            </Card>
                        </motion.div>
                    )}
                </AnimatePresence>
            </div>

            {/* Draw Offer Modal */}
            <AnimatePresence>
                {drawOffered && drawOfferedBy && (
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        className="fixed inset-0 bg-black/80 flex items-center justify-center z-50"
                    >
                        <Card className="p-8 text-center max-w-md">
                            <Handshake className="w-16 h-16 text-accent mx-auto mb-4" />
                            <h2 className="font-display text-2xl mb-2">DRAW OFFERED</h2>
                            <p className="text-foreground-muted mb-6">
                                {drawOfferedBy} has offered a draw.
                            </p>
                            <div className="flex gap-4 justify-center">
                                <Button onClick={() => handleDrawResponse(true)}>
                                    ACCEPT
                                </Button>
                                <Button variant="secondary" onClick={() => handleDrawResponse(false)}>
                                    DECLINE
                                </Button>
                            </div>
                        </Card>
                    </motion.div>
                )}
            </AnimatePresence>

            {/* Game Result Modal */}
            <AnimatePresence>
                {gameResult && (
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        className="fixed inset-0 z-50"
                    >
                        {/* Determine if this is victory, defeat, or draw */}
                        {(() => {
                            const isVictory = gameResult.winner === playerColor;
                            const isDraw = gameResult.winner === "draw";
                            const isDefeat = !isVictory && !isDraw;

                            if (isVictory) {
                                // Victory Screen
                                return (
                                    <div className="w-full h-full bg-accent flex items-center justify-center corner-brackets">
                                        <motion.div
                                            initial={{ scale: 0.8, opacity: 0 }}
                                            animate={{ scale: 1, opacity: 1 }}
                                            className="text-center"
                                        >
                                            <h1 className="font-display text-9xl text-white mb-4">VICTORY</h1>
                                            <p className="text-white/80 uppercase tracking-widest mb-2 text-xl">
                                                {gameResult.reason.replace(/_/g, " ")}
                                            </p>
                                            <p className="text-white/60 uppercase tracking-widest mb-8">
                                                {moveHistory.length} MOVES
                                            </p>
                                            <div className="flex gap-4 justify-center">
                                                <Button className="bg-black hover:bg-gray-900" size="lg">
                                                    <Crown className="w-5 h-5" />
                                                    REMATCH
                                                </Button>
                                                <Button
                                                    variant="outline"
                                                    size="lg"
                                                    className="border-white text-white hover:bg-white/10"
                                                    onClick={() => router.push("/dashboard")}
                                                >
                                                    LOBBY
                                                </Button>
                                            </div>
                                        </motion.div>
                                    </div>
                                );
                            } else if (isDraw) {
                                // Draw Screen
                                return (
                                    <div className="w-full h-full bg-background flex items-center justify-center">
                                        <motion.div
                                            initial={{ scale: 0.8, opacity: 0 }}
                                            animate={{ scale: 1, opacity: 1 }}
                                            className="text-center"
                                        >
                                            <h1 className="font-display text-8xl text-foreground-muted mb-4">DRAW</h1>
                                            <p className="text-foreground-dim uppercase tracking-widest mb-8">
                                                {gameResult.reason.replace(/_/g, " ")}
                                            </p>
                                            <div className="flex gap-4 justify-center">
                                                <Button size="lg">REMATCH</Button>
                                                <Button variant="secondary" size="lg" onClick={() => router.push("/dashboard")}>
                                                    LOBBY
                                                </Button>
                                            </div>
                                        </motion.div>
                                    </div>
                                );
                            } else {
                                // Defeat Screen
                                return (
                                    <div className="w-full h-full bg-background flex items-center justify-center">
                                        <motion.div
                                            initial={{ scale: 0.8, opacity: 0 }}
                                            animate={{ scale: 1, opacity: 1 }}
                                            className="text-center relative"
                                        >
                                            <div className="absolute top-1/2 left-0 right-0 h-1 bg-accent transform -rotate-6" />
                                            <h1 className="font-display text-9xl text-white mb-4 relative line-through decoration-accent">
                                                DEFEAT
                                            </h1>
                                            <p className="text-accent uppercase tracking-widest mb-2 text-xl">
                                                {gameResult.reason.replace(/_/g, " ")}
                                            </p>
                                            <p className="text-foreground-dim uppercase tracking-widest mb-8">
                                                THE KING HAS FALLEN.
                                            </p>
                                            <div className="flex gap-4 justify-center">
                                                <Button className="bg-accent hover:bg-accent-light" size="lg">REMATCH</Button>
                                                <Button variant="secondary" size="lg" onClick={() => router.push("/dashboard")}>
                                                    ANALYSIS
                                                </Button>
                                            </div>
                                        </motion.div>
                                    </div>
                                );
                            }
                        })()}
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
}
